@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'BetonSans';
  src: local('BetonSans'), url(/marvel-app/static/media/BentonSans-Comp-Black-Regular.697ae783.otf) format('truetype');
}

.backgroundImg{overflow:hidden;height:100vh;width:100vw;background-repeat:no-repeat;background-position:top;background-size:cover;-webkit-filter:blur(5px);filter:blur(5px);transform:scale(1.1)}.backgroundImgContainer{overflow:hidden;height:100vh;width:100%;top:0;left:0;position:absolute;z-index:10}.imageOverlay{height:100%;width:100%;position:absolute;background-color:rgba(28,28,29,0.3);top:0;left:0}.welcomeSection{background-color:transparent;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(15px + 2vmin);font-weight:bold;color:white;position:relative;z-index:100}.welcomeSectionContainer{display:flex;align-items:center;width:100%;flex-direction:row;justify-content:space-evenly}.welcomeSectionContainer-tablet{width:75vmin}.welcomeSectionContainer-mobile{width:100%;display:flex;align-items:center;flex-direction:column;justify-content:center}.welcomeSectionTxt{align-self:center;max-width:512px}.welcomeSectionTxtAppear{animation:appear 1.5s ease-out both;-webkit-animation:appear 1.5s ease-out both}@keyframes appear{0%{-webkit-opacity:0;opacity:0}100%{-webkit-opacity:1;opacity:1}}.welcomeSectionTxt-tablet{width:100%;margin-top:64px;align-self:center}.welcomeSectionTxt-mobile{margin-top:64px;align-self:center}.animatedImg{pointer-events:none;width:60vmin}.animatedImg-tablet{width:100%}.animatedImgSlideRight{animation:slideRight 1s ease-out both;-webkit-animation:slideRight 1s ease-out both}@keyframes slideRight{0%{transform:translateX(-100px);-webkit-opacity:0;opacity:0}100%{transform:translateX(0);-webkit-opacity:1;opacity:1}}

.navBar{height:64px;position:fixed;top:0;left:0;width:100%;z-index:100000;background-color:#202020;display:flex;flex-direction:row;align-items:center}.navBar-mobile{height:48px;align-items:flex-start;justify-content:space-between}.marvelLogo{height:100%;background-color:#F0141E}.marvelLogo-mobile{display:flex;justify-content:center;align-items:center}.burgerButton{display:block;align-self:center;cursor:pointer}.burgerButtonContainer{display:flex;justify-content:flex-end;height:32px}.sideBarMenu{background-color:#353434;justify-content:flex-start;width:128px;height:100vh;display:flex;flex-direction:column}.menuItem{display:inline-block;padding-right:16px;padding-left:16px}.menuItem::after{content:'';width:0px;height:2px;display:block;background:#fff;transition:300ms}.menuItem:hover{color:#F0141E}.menuItem:hover::after{width:100%}.marvelDefaultText{color:white;font-family:BetonSans, sans-serif;font-size:32px;text-decoration:none;align-self:center;text-transform:uppercase}

